<template>
    <div>
        <!--  -->
        <order-nav-bar :navBarItem="['', '', '']" />
        <!--  -->
        <el-card>
            <!-- API -->
            <el-alert title="" type="warning" :closable="false" show-icon></el-alert>
            <br />
            <el-row>
                <el-input v-model="searchVal" placeholder="">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </el-row>
            <!--  -->
            <order-list :orderList="orderList" />

            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[3, 5, 8, 10]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total"
                background
            ></el-pagination>
        </el-card>
        <!--  -->
        <edit-address />
        <!--  -->
        <logistics-process />
    </div>
</template>

<script>
import OrderList from './childOrder/OrderList'
import EditAddress from './childOrder/EditAddress'
import LogisticsProcess from './childOrder/LogisticsProcess'

import orderNavBar from '../../components/breadNav/BreadNavBar'
import { getOrderList } from '../../network/orders'

export default {
    components: {
        OrderList,
        EditAddress,
        LogisticsProcess,
        orderNavBar,
    },
    data() {
        return {
            // 
            queryInfo: {
                query: '',  // 
                pagenum: 1,  // 
                pagesize: 5,  // 
            },
            total: 0, // 
            orderList: [],  // 
            searchVal: '',  // 
        }
    },
    mounted() {
        // 
        this.get_OrderList()
    },
    methods: {
        /*  */
        // 
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize
            // 
            this.get_OrderList()
        },
        // 
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage
            // 
            this.get_OrderList()
        },


        /*  */
        // 
        async get_OrderList() {
            let { query, pagenum, pagesize } = this.queryInfo
            let { data, meta } = await getOrderList(query, pagenum, pagesize)
            this.orderList = data.goods
            this.total = data.total
        }
    },
};
</script>

<style scoped>
.el-input {
    width: 550px;
}
</style>
