<template>
    <el-table :data="orderList" border>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column prop="order_number" label=""></el-table-column>
        <el-table-column label="">
            <template v-slot="scope">
                <el-tag v-show="scope.row.pay_status == 1" type="success"></el-tag>
                <el-tag v-show="scope.row.pay_status == 0" type="danger"></el-tag>
            </template>
        </el-table-column>
        <el-table-column prop="order_price" label=""></el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column label="">
            <template v-slot="scope">
                <span>{{ scope.row.create_time | dateFormat }}</span>
            </template>
        </el-table-column>
        <el-table-column label="">
            <template v-slot="scope">
                <el-tooltip effect="dark" content="" placement="top">
                    <!--  -->
                    <el-button
                        type="primary"
                        icon="el-icon-edit"
                        circle
                        size="small"
                        @click="editClick(scope.row)"
                    ></el-button>
                </el-tooltip>

                <el-tooltip effect="dark" content="" placement="top">
                    <!--  -->
                    <el-button
                        type="success"
                        icon="el-icon-location"
                        circle
                        size="small"
                        @click="logisticsClick(scope.row)"
                    ></el-button>
                </el-tooltip>
            </template>
        </el-table-column>
    </el-table>
</template>

<script>
export default {
    props: {
        orderList: Array
    },
    methods: {
        // 
        logisticsClick(orderInfo) {
            this.$bus.$emit('logisticsClick', orderInfo)
        },
        // 
        editClick(orderInfo) {
            this.$bus.$emit('editClick', orderInfo)
        }
    },
};
</script>

